.front {

  .region-content .content {
    font-size: 1rem;
    line-height: 1.5;
    text-align: center;
    color: #4A4A4A;
    margin: 0 auto 1em;
    max-width: 720px;
  }

  h1.page__title {
    font-weight: 700;
    text-align: center;
    font-size: 20px;
    font-family: $cd-font;
    margin: 0 0 1rem;
    text-transform: uppercase;
    color: #026CB6;
  }

  .intro,
  h2 {
    font-size: 16px;
    text-align: center;
    font-weight: 400;
    color: #4A4A4A;
    margin: 0 0 40px;
    font-family: $cd-font;
  }

  .block h2 {
    margin: 0;
  }

  form {
    margin-bottom: 40px;
  }

  .views-exposed-widgets {
    margin-bottom: 0;
  }

  label {
    display: none;
  }

  input[type=text] {
    @include font-size($cd-font-size-medium);
    font-family: $cd-font;
    font-weight: 300;
    color: #4A4A4A;
    height: 40px;
    line-height: 40px;

    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
      color: #4A4A4A;
      font-style: italic;
    }
    &::-moz-placeholder { /* Firefox 19+ */
      color: #4A4A4A;
      font-style: italic;
    }
    &:-ms-input-placeholder { /* IE 10+ */
      color: #4A4A4A;
      font-style: italic;
    }
    &:-moz-placeholder { /* Firefox 18- */
      color: #4A4A4A;
      font-style: italic;
    }
  }

  .node-page p {
    text-align: left;
    font-size: 14px;
    line-height: 1.8;

    &.centered,
    &.rtecenter {
      text-align: center;
    }
  }

  .btn--browse {
    display: inline-block;
    border-radius: 4px;
    background: #026CB6;
    text-transform: uppercase;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    padding: 0 12px;
    text-decoration: none;
    transition: background .3s ease;
    line-height: 40px;
    margin: 1em auto 0;

    &:hover, &:focus {
      background: darken(#026CB6, 7%);
    }
  }

  .ds-2col-stacked-fluid.group-one-column > .group-left {
    padding-right: 0;
  }

}

.front .content {
  .views-exposed-form .views-exposed-widget {
    display: block;
    float: none;
    padding: 0;
    text-align: center;
    max-width: 40rem;
    margin: 0 auto;

    .form-item {
      background: transparent;
      padding: 0;
      margin: 0;
    }

    .form-submit {
      margin-top: 20px;
    }
  }

}

@include sm {
  .front .content {
    .views-exposed-form .views-exposed-widget {
      display: inline-block;
      margin: 0;
    }

    .views-exposed-form .views-submit-button {
      margin-left: -4px;

      .form-submit {
        border-radius: 0 4px 4px 0;
        margin-top: 0;
      }
    }
  }
}
